
.App {
  text-align: left;
  padding   : 0 10px;

}

.App-link {
  color: #61dafb;
}


button.btn {
  margin-right: 4px;
}

span.badge {
  margin-left: 4px;
}

button.hostPlayerOption.btn {
  padding: 1px 2px;
  border: none;
}

ol {
  padding-left: 20px;
}


@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .spinner {
    animation: spin infinite 4s linear;
  }
}

div.alert.validationError {
  padding: 4px; 
  margin-top: 4px;
}